.select {
	width: 100%;
	position: relative;
	margin-bottom: 20px;
	user-select: none;
	z-index: 1;

	&.opened {
		z-index: 1;

		.select__block {
			&:before {
				transform: rotate(90deg);
				//top: 17px;
			}
		}

		.select__list {
			transform: scaleY(1);
		}
	}

	&__block {
		//height: 35px;
		width: 100%;
		padding: 0 15px;
		background-color: white;
		line-height: 35px;
		cursor: pointer;
		border: 2px solid rgba(black,0.4);

		&:before,
		&:after{
			content: '';
			position: absolute;
			width: 2px;
			height: 15px;
			top: 10px;
			right: 20px;
			//border: 2px solid transparent;
			//border-right-color: black;
			//border-bottom-color: black;
			background-color: black;
			transition: all 0.2s ease-out;
			transform-origin: center;
		}
		&:after {
			content: '';
			position: absolute;
			transform: rotate(90deg);
			//border: 2px solid transparent;
			//border-right-color: black;
			//border-bottom-color: black;
			//transform: rotate(45deg);
			transition: all 0.2s ease-out;
			transform-origin: center;
		}
	}

	&__list {
		transform: scaleY(0);
		transform-origin: top center;
		transition: all 0.2s ease-out;
		position: absolute;
		width: 100%;
		top: calc(100% + 2px);
		left: 0;
		border: 2px solid rgba(black,0.4);
		background-color: white;
		padding: 10px;
	}

	&__option {
		line-height: 30px;
		height: 30px;
		cursor: pointer;
		width: 100%;
		padding: 0 10px;

		&.selected {
			background-color: #C4C4C4;
			color: white;
		}
	}
}
