.posts {
	position: relative;

	&::before {
		position: absolute;
		content: '';
		height: 100%;
		width: 100%;
		background-color: #74f46b;
		z-index: -1;
		padding-bottom: 10%;
	}

	&__list {
		padding-top: 50px;
		display: flex;
		flex-wrap: wrap;

		margin: 0 -10px;

	}

	&__item {
		border: 2px solid rgba(black, 0.4);
		width: calc(33.33% - 20px);
		padding: 10px;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		flex-shrink: 1;
		margin: 0 10px;
		min-height: 250px;
	}

	&__title {
		margin-top: 5px;
		margin-bottom: 5px;
	}


	&__text {
	}

	&__date {
		margin-top: auto;

	}

	&__note {

	}

}
